import { PanelContainer, LabelGroup, RangeInput, SettingTitle } from "./styles"; // Import SettingTitle

export default function SettingsPanel({
  setWordType,
  playbackSpeed,
  setPlaybackSpeed,
  playbackOptions,
  setPlaybackOptions,
  proficiencyFilter,
  setProficiencyFilter,
  autoProceed,
  setAutoProceed,
  startQuestionIndex,
  setStartQuestionIndex,
  wordRangeCount,
  setWordRangeCount,
  sortOrder,
  wordType,
  setSortOrder,
  learningSteps, // New Anki prop
  setLearningSteps, // New Anki prop
  graduatingInterval, // New Anki prop
  setGraduatingInterval, // New Anki prop
  lapseInterval, // New Anki prop
  setLapseInterval, // New Anki prop
  isQuizContext,
}) {
  const handleProficiencyChange = (level) => {
    setProficiencyFilter({
      ...proficiencyFilter,
      [level]: !proficiencyFilter[level],
    });
  };

  return (
    <PanelContainer>
      <LabelGroup>
        <SettingTitle>: {playbackSpeed}</SettingTitle>
        <RangeInput
          type="range"
          min="0.2"
          max="2"
          step="0.1"
          value={playbackSpeed}
          onChange={(e) => setPlaybackSpeed(Number(e.target.value))}
        />
      </LabelGroup>

      <LabelGroup>
        <SettingTitle></SettingTitle>
        <div>
          <label>
            <input
              type="checkbox"
              checked={playbackOptions.jp}
              onChange={(e) =>
                setPlaybackOptions({
                  ...playbackOptions,
                  jp: e.target.checked,
                })
              }
            />
            
          </label>
          <label>
            <input
              type="checkbox"
              checked={playbackOptions.ch}
              onChange={(e) =>
                setPlaybackOptions({
                  ...playbackOptions,
                  ch: e.target.checked,
                })
              }
            />
            
          </label>
          <label>
            <input
              type="checkbox"
              checked={playbackOptions.jpEx}
              onChange={(e) =>
                setPlaybackOptions({
                  ...playbackOptions,
                  jpEx: e.target.checked,
                })
              }
            />
            
          </label>
          <label>
            <input
              type="checkbox"
              checked={playbackOptions.chEx}
              onChange={(e) =>
                setPlaybackOptions({
                  ...playbackOptions,
                  chEx: e.target.checked,
                })
              }
            />
            
          </label>
        </div>
      </LabelGroup>
      {autoProceed !== undefined && (
        <LabelGroup>
          <SettingTitle></SettingTitle>
          <label>
            <input
              type="checkbox"
              checked={autoProceed}
              onChange={(e) => setAutoProceed(e.target.checked)}
            />
            
          </label>
        </LabelGroup>
      )}

      {!isQuizContext && ( // Conditionally render
        <LabelGroup>
          <SettingTitle>:</SettingTitle>
          <div>
            <label>
              <input
                type="checkbox"
                checked={proficiencyFilter[1]}
                onChange={() => handleProficiencyChange(1)}
              />
              
            </label>
            <label>
              <input
                type="checkbox"
                checked={proficiencyFilter[2]}
                onChange={() => handleProficiencyChange(2)}
              />
              
            </label>
            <label>
              <input
                type="checkbox"
                checked={proficiencyFilter[3]}
                onChange={() => handleProficiencyChange(3)}
              />
              
            </label>
          </div>
        </LabelGroup>
      )}
      {!isQuizContext && (
        <LabelGroup>
          <SettingTitle>:</SettingTitle>
          <div>
            <label>
              <input
                type="radio"
                name="sortOrder"
                value="none"
                checked={sortOrder === "none"}
                onChange={(e) => setSortOrder(e.target.value)}
              />
              
            </label>
            <label>
              <input
                type="radio"
                name="sortOrder"
                value="random"
                checked={sortOrder === "random"}
                onChange={(e) => setSortOrder(e.target.value)}
              />
              
            </label>
            <label>
              <input
                type="radio"
                name="sortOrder"
                value="aiueo"
                checked={sortOrder === "aiueo"}
                onChange={(e) => setSortOrder(e.target.value)}
              />
              
            </label>
          </div>
        </LabelGroup>
      )}
      {!isQuizContext && ( // Conditionally render startQuestionIndex
        <LabelGroup>
          <SettingTitle>:</SettingTitle>
          <input
            type="number"
            min="1"
            value={startQuestionIndex}
            onChange={(e) => setStartQuestionIndex(Number(e.target.value))}
          />
        </LabelGroup>
      )}

      {!isQuizContext && ( // Conditionally render wordRangeCount
        <LabelGroup>
          <SettingTitle>:</SettingTitle>
          <input
            type="number"
            min="1"
            value={wordRangeCount}
            onChange={(e) => setWordRangeCount(Number(e.target.value))}
          />
        </LabelGroup>
      )}
      {!isQuizContext && (
        <LabelGroup>
          <SettingTitle>:</SettingTitle>
          <div>
            <label>
              <input
                type="radio"
                name="wordType"
                value="jp_word"
                checked={wordType === "jp_word"}
                onChange={(e) => setWordType(e.target.value)}
              />
              /
            </label>
            <label>
              <input
                type="radio"
                name="wordType"
                value="kanji_jp_word"
                checked={wordType === "kanji_jp_word"}
                onChange={(e) => setWordType(e.target.value)}
              />
              
            </label>
          </div>
        </LabelGroup>
      )}

      {/* Anki Algorithm Settings */}
      {/* {learningSteps !== undefined && ( // Conditionally render Anki settings
        <>
          <SettingTitle>Anki </SettingTitle>
          <LabelGroup>
            <SettingTitle> (, ):</SettingTitle>
            <input
              type="text"
              value={learningSteps.join(", ")}
              onChange={(e) =>
                setLearningSteps(
                  e.target.value.split(",").map((s) => Number(s.trim()))
                )
              }
            />
          </LabelGroup>
          <LabelGroup>
            <SettingTitle> ():</SettingTitle>
            <input
              type="number"
              min="1"
              value={graduatingInterval}
              onChange={(e) => setGraduatingInterval(Number(e.target.value))}
            />
          </LabelGroup>
          <LabelGroup>
            <SettingTitle> ():</SettingTitle>
            <input
              type="number"
              min="1"
              value={lapseInterval}
              onChange={(e) => setLapseInterval(Number(e.target.value))}
            />
          </LabelGroup>
        </>
      )} */}
    </PanelContainer>
  );
}
